// src/InvestmentTools.scss

// --- Variables ---
$primary-text: #2c3e50;
$secondary-text: #596275;
$accent-color: #007bff; // Example: Blue
$accent-color-dark: #0056b3;
$background-color: #f4f7f6; // Light greyish background for the page
$card-background: #ffffff;
$border-color: #e0e0e0;
$error-color: #d32f2f;
$success-color: #388e3c;
$info-color-bg: #e3f2fd;
$info-color-border: #90caf9;

$breakpoint-sm: 576px; // Small devices (landscape phones)
$breakpoint-md: 768px; // Medium devices (tablets)
$breakpoint-lg: 992px; // Large devices (desktops)
$breakpoint-xl: 1200px; // Extra large devices (large desktops)

// --- Mixins ---
@mixin card-styling {
  background-color: $card-background;
  border: 1px solid $border-color;
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 25px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.05);
}

// --- Base Styles for the Page ---
.investment-tools-page {
  // This is the top-level div in your component
  // If it shares .dashboard-page, those styles will also apply.
  // Assuming .dashboard-page might set up a flex layout with Sidebar
}

.main-content-investment-tool {
  display: flex; // For Sidebar and tool-container layout
  width: 100%;

  // Sidebar styling should be in Sidebar.scss
  // Assuming Sidebar component has a fixed width or handles its own width
  // e.g., .Sidebar { flex-shrink: 0; width: 250px; }
}

.tool-container {
  flex-grow: 1; // Takes remaining space beside the sidebar
  padding: 25px;
  background-color: $background-color;
  min-height: calc(100vh - 60px); // Adjust if you have a global header of a certain height

  h2 {
    color: $primary-text;
    margin-bottom: 20px;
    font-size: 2em; // Larger heading
    font-weight: 600;
    border-bottom: 2px solid $accent-color;
    padding-bottom: 10px;
  }

  .user-greeting {
    background-color: $info-color-bg;
    border-left: 4px solid $info-color-border;
    color: $secondary-text;
    padding: 10px 15px;
    margin-bottom: 20px;
    border-radius: 4px;
    font-size: 0.95em;
  }

  .api-selector-wrapper { // Wrapper for API selector for card styling
    @include card-styling;
  }

  .api-selector {
    display: flex;
    align-items: center;
    gap: 15px; // Space between label and select

    label {
      font-weight: 500;
      color: $primary-text;
      margin-bottom: 0; // Reset margin if it's a flex item
    }

    select {
      padding: 10px 15px;
      border-radius: 5px;
      border: 1px solid $border-color;
      font-size: 1em;
      background-color: $card-background;
      color: $primary-text;
      min-width: 250px; // Give it some default width
      transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;

      &:focus {
        outline: none;
        border-color: $accent-color;
        box-shadow: 0 0 0 0.2rem rgba($accent-color, 0.25);
      }
    }
  }

  .input-form-container { // Wrapper for InputForm component
    @include card-styling;
    // Further styling depends on InputForm's internal structure.
    // If InputForm uses a <form> tag:
    form {
        display: flex;
        flex-wrap: wrap; // Allow items to wrap
        gap: 20px; // Space between form groups and button

        // Assuming InputForm renders individual fields within a div or similar
        // e.g., <div class="form-group"> <label/> <input/> </div>
        .form-group { // Target this if InputForm generates it
            flex: 1 1 200px; // Grow, shrink, with a base width
            display: flex;
            flex-direction: column;
            min-width: 180px; // Don't let them get too squished

            label {
                margin-bottom: 6px;
                font-size: 0.9em;
                color: $secondary-text;
                font-weight: 500;
            }

            input[type="text"],
            input[type="search"], // If using search for symbol
            select { // If InputForm uses selects
                padding: 10px 12px;
                border: 1px solid $border-color;
                border-radius: 5px;
                font-size: 1em;
                &:focus {
                    outline: none;
                    border-color: $accent-color;
                    box-shadow: 0 0 0 0.2rem rgba($accent-color, 0.25);
                }
            }
        }

        button[type="submit"] {
            padding: 10px 25px;
            background-color: $accent-color;
            color: white;
            border: none;
            border-radius: 5px;
            cursor: pointer;
            font-size: 1em;
            font-weight: 500;
            transition: background-color 0.2s ease-in-out;
            align-self: flex-end; // Align with bottom of input fields
            min-height: 42px; // Match input height roughly

            &:hover:not(:disabled) {
                background-color: $accent-color-dark;
            }
            &:disabled {
                background-color: #ccc;
                cursor: not-allowed;
            }
        }
    }
  }

  .data-display-area {
    margin-top: 30px;

    .no-data-message {
      @include card-styling;
      text-align: center;
      color: $secondary-text;
      font-style: italic;
      padding: 30px;
    }

    // DataCard would have its own styling from DataCard.scss
    // DataTable would have its own styling from DataTable.scss
    // We can style the container for DataTable here for overflow
    .data-table-container { // Wrapper for DataTable component
        @include card-styling;
        overflow-x: auto; // IMPORTANT: for table horizontal scrolling
        padding: 0; // DataTable might have internal padding

        table {
            width: 100%; // Ensure table tries to use full width
            min-width: 600px; // Prevent table from collapsing too much, forcing scroll
        }
    }
  }
}

// --- Auth Message Styling ---
.auth-message-container { // Applied in JS when not authenticated
  padding: 30px;
  text-align: center;
  margin: 20px auto; // Center it if sidebar is not there
  max-width: 600px;
  @include card-styling;
  border-left: 5px solid $error-color;

  h2 {
    color: $error-color;
    border-bottom: none;
    margin-bottom: 15px;
  }
  // ErrorMessage component will have its own styling
}


// --- Responsive Adjustments ---

// Extra Small devices (portrait phones, less than 576px)
@media (max-width: #{$breakpoint-sm - 1px}) {
  .tool-container {
    padding: 15px;
    h2 { font-size: 1.6em; }

    .api-selector {
        flex-direction: column;
        align-items: stretch;
        gap: 10px;
        select { min-width: 100%; }
    }

    .input-form-container form {
        flex-direction: column;
        align-items: stretch; // Make form elements full width
        .form-group {
            flex-basis: auto; // Override flex-basis
            width: 100%;
        }
        button[type="submit"] {
            width: 100%;
            margin-top: 10px; // Add some space when stacked
            align-self: auto;
        }
    }
  }
}

// Small devices (landscape phones, 576px and up)
@media (min-width: $breakpoint-sm) and (max-width: #{$breakpoint-md - 1px}) {
  .tool-container {
    padding: 20px;
    h2 { font-size: 1.7em; }

    .api-selector {
        flex-direction: column;
        align-items: stretch;
        gap: 10px;
        select { min-width: 100%; }
    }

    .input-form-container form {
        .form-group {
             // Allow two items per row if space
            flex-basis: calc(50% - 10px); // 10px is half of gap
        }
        button[type="submit"] {
            // Keep align-self: flex-end or stretch if preferred
        }
    }
  }
}

// Medium devices (tablets, 768px and up)
// This is where the sidebar might become permanently visible if it's collapsible
@media (min-width: $breakpoint-md) {
  .main-content-investment-tool {
    // Assuming sidebar takes its width and tool-container flex-grows
  }
  .tool-container {
    .api-selector {
        flex-direction: row; // Back to row layout
        align-items: center;
        select { min-width: 250px; } // Restore min-width
    }
    .input-form-container form {
        flex-direction: row; // Back to row layout for form elements
        align-items: flex-end; // Align items to bottom
        .form-group {
            // Default flex: 1 1 200px; is good here
        }
        button[type="submit"] {
            width: auto; // Reset width
            margin-top: 0;
            align-self: flex-end;
        }
    }
  }
}

// Large devices (desktops, 992px and up)
@media (min-width: $breakpoint-lg) {
  .tool-container {
    padding: 30px; // More padding on larger screens
  }
}