// src/Component/AdminDashboard/AdminOverview.scss
// src/assets/styles/_variables.scss (or a similar shared location)

// Colors
$primary-color: #3498db;      // A general primary color (e.g., blue for links, buttons)
$sidebar-bg: #2c3e50;         // Dark blue/gray for sidebar
$sidebar-text-color: #ecf0f1;
$sidebar-active-bg: #34495e;
$sidebar-hover-bg: #3b5168;

$header-bg: #ffffff;
$header-text-color: #333;
$header-border-color: #e8e8e8;

$content-bg: #f0f2f5;          // Main background for content area
$card-bg: #ffffff;
$card-shadow: 0 2px 8px rgba(0, 0, 0, 0.09);
$card-hover-shadow: 0 4px 12px rgba(0, 0, 0, 0.12);

$text-primary: #2c3e50;
$text-secondary: #576574;
$text-light: #7f8c8d;

$border-color: #e0e0e0;
$table-header-bg: #f8f9fa;
$table-row-hover-bg: #e9ecef;

$error-bg: #fbebeb;
$error-text: #c0392b;
$error-border: #e74c3c;
$success-color: #27ae60;
$accent-color-blue: #4858C2; // From your card styling

// Typography
$font-family-sans: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
  Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
$font-size-base: 1rem;     // 16px
$font-size-sm: 0.875rem; // 14px
$font-size-lg: 1.25rem;  // 20px

// Layout
$sidebar-width-desktop: 260px;
$sidebar-width-mobile-open: 240px; // Slightly narrower for mobile overlay
$header-height: 60px;

// Breakpoints
$bp-sm: 576px;  // Small devices (landscape phones)
$bp-md: 768px;  // Medium devices (tablets) - Often sidebar toggle point
$bp-lg: 992px;  // Large devices (desktops)
$bp-xl: 1200px; // Extra large devices

// Transitions
$transition-speed: 0.3s;
$transition-easing: ease-in-out;

.admin-overview-content { // Wrapper from AdminOverview.jsx
  // General page title if AdminOverview has its own H2
  .page-title { // Assuming you add <h2 className="page-title">Dashboard Overview</h2>
    font-size: $font-size-lg + 0.2rem;
    color: $text-primary;
    margin-bottom: 20px;
    font-weight: 600;
  }
}

.cards-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 24px; // Consistent gap
}

.card.admin-overview-card { // Specific to overview cards
  background-color: $card-bg;
  border-radius: 8px;
  padding: 20px;
  box-shadow: $card-shadow;
  transition: box-shadow $transition-speed $transition-easing, transform $transition-speed $transition-easing;
  display: flex;
  flex-direction: column; // Allow content to stack and button to be at bottom

  &:hover {
    box-shadow: $card-hover-shadow;
    transform: translateY(-4px);
  }

  .card-header h3 { // If you wrap title in a header
    margin: 0 0 10px;
    color: $text-primary;
    font-size: 1.1rem;
    font-weight: 600;
  }

  .card-description {
    color: $text-secondary;
    font-size: $font-size-sm;
    margin-bottom: 15px;
    line-height: 1.5;
    flex-grow: 1; // Allows description to push button down if needed
  }

  .card-content { // For main data points
    h2 { // Large figures like $20,500.00
      font-size: 1.9rem;
      color: $accent-color-blue; // Using the blue from your example
      margin: 5px 0 10px;
      font-weight: 700;
    }
    h4 { // Sub-titles like "Active Plan"
      font-size: $font-size-base;
      font-weight: 500;
      color: $text-primary;
      margin-top: 15px;
      margin-bottom: 5px;
    }
    p {
      color: $text-light;
      font-size: $font-size-sm;
      margin: 4px 0;
    }
  }

  // Specific card styling: Progress Circle
  .progress-circle {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;

    .progress-title { // For text like "Mercury"
      font-weight: bold;
      color: $text-primary;
      margin-bottom: 8px;
      font-size: $font-size-base;
    }

    .circle-container { // Your "circle" div
      width: 100px;
      height: 100px;
      border-radius: 50%;
      background-color: #e0e0e0; // Track color
      position: relative;
      margin: 10px 0;
      // For a proper circular progress bar, you'd typically use SVG or conic-gradient
      // This basic fill is for demonstration.
      .fill {
        background-color: $accent-color-blue;
        position: absolute;
        top: 0; left: 0;
        height: 100%;
        // width: will be set by inline style e.g. style={{ width: "100%" }}
        border-radius: 50%; // This will look odd if width is not 100% for a simple fill
        // For a partial fill (e.g., 75%), it's better to use clip-path or SVG arc.
      }
      .circle-percentage-text { // For text like "100%" inside circle
        position: absolute;
        top: 50%; left: 50%;
        transform: translate(-50%, -50%);
        color: $card-bg; // White if fill is dark
        font-weight: bold;
        font-size: $font-size-base;
      }
    }
    .progress-details { // Text like "4 – 100%"
      font-size: $font-size-sm;
      color: $text-secondary;
    }
  }

  // Logout button example in a card
  .card-action-button {
    background-color: $error-border; // Using your error color
    color: white;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: auto; // Pushes button to the bottom of the card
    font-size: $font-size-sm;
    align-self: flex-start; // Aligns button to the left if card is flex container
    transition: background-color $transition-speed $transition-easing;

    &:hover {
      background-color: darken($error-border, 10%);
    }
  }
}

// Responsive adjustments for cards
@media (max-width: $bp-sm) {
  .cards-container {
    grid-template-columns: 1fr; // Stack cards
    gap: 16px;
  }
  .card.admin-overview-card {
    padding: 15px;
    .card-content h2 {
      font-size: 1.7rem;
    }
  }
}