// src/Component/AdminDashboard/UserManagement.scss

// --- Variables (Defined directly in this file) ---
// Colors
$primary-color: #3498db;      // Main accent for buttons, links
$sidebar-bg: #2c3e50;         // Example, not directly used here but good for context
$header-bg: #ffffff;
$content-bg: #f0f2f5;         // Page background if not overridden by card style
$card-bg: #ffffff;             // Background for the main content block/table container
$card-shadow: 0 4px 12px rgba(0,0,0,0.05); // Softer shadow

$text-primary: #2c3e50;        // Main text (dark grayish blue)
$text-secondary: #576574;      // Lighter gray for less emphasis
$text-light: #7f8c8d;

$border-color: #e0e0e0;        // General borders
$border-color-light: lighten($border-color, 5%); // Lighter table cell borders

$table-header-bg: lighten(#f0f4f7, 2%); // Very light, almost white header
$table-row-hover-bg: lighten($primary-color, 40%); // Subtle blue hover

$error-bg: #fbebeb;
$error-text: #c0392b;
$error-border: #e74c3c;
$success-color: #27ae60;       // Green for "verified"

// Typography
$font-family-sans: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
  Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
$font-size-base: 1rem;     // 16px
$font-size-sm: 0.875rem;   // 14px
$font-size-lg: 1.25rem;    // 20px

// Layout & Spacing (using common names for clarity)
$spacing-xs: 0.25rem; // 4px
$spacing-sm: 0.5rem;  // 8px
$spacing-md: 1rem;    // 16px
$spacing-lg: 1.5rem;  // 24px
$spacing-xl: 2rem;    // 32px

// Breakpoints
$bp-sm: 576px;
$bp-md: 768px;
$bp-lg: 992px;

// Transitions
$transition-speed: 0.2s; // Slightly faster for snappier feel
$transition-easing: ease-in-out;

// --- User Management Page Styles ---
.user-management-page {
  font-family: $font-family-sans;
  padding: $spacing-lg;

  @media (max-width: $bp-sm) {
    padding: $spacing-md;
  }

  // Page Header Area (Title + Optional Actions)
  .page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $spacing-lg;
    padding-bottom: $spacing-md; // Space below title area
    border-bottom: 1px solid $border-color-light; // Subtle separator

    .page-title {
      font-size: 1.8rem;
      color: $text-primary;
      font-weight: 600;
      margin: 0;
    }

    .header-actions button {
      background-color: $primary-color;
      color: #fff;
      border: none;
      padding: $spacing-sm $spacing-md;
      border-radius: 6px;
      font-size: $font-size-sm;
      font-weight: 500;
      cursor: pointer;
      transition: background-color $transition-speed $transition-easing, box-shadow $transition-speed $transition-easing;
      box-shadow: 0 2px 4px rgba(darken($primary-color, 15%), 0.2);

      &:hover {
        background-color: darken($primary-color, 10%);
        box-shadow: 0 4px 8px rgba(darken($primary-color, 15%), 0.25);
      }
    }
  }

  // Status Messages (Loading, No Users)
  .status-message {
    text-align: center;
    color: $text-secondary;
    font-size: 1.1rem;
    padding: $spacing-xl $spacing-lg;
    background-color: $card-bg;
    border-radius: 8px;
    box-shadow: $card-shadow;
  }

  // Error Message Styling
  .error-message {
    background-color: lighten($error-bg, 5%);
    color: $error-text;
    padding: $spacing-md;
    border-radius: 6px;
    border: 1px solid lighten($error-border, 10%);
    border-left: 4px solid $error-border;
    margin-bottom: $spacing-lg;
    font-size: $font-size-sm;
    display: flex;
    align-items: center;
    // .icon { margin-right: $spacing-sm; /* For an error icon */ }
  }

  // Table Container & Styling
  .table-container {
    background-color: $card-bg;
    border-radius: 10px;
    box-shadow: $card-shadow;
    overflow: hidden;
    margin-bottom: $spacing-lg;

    .table-responsive-wrapper {
      overflow-x: auto;
      width: 100%;

      .user-table {
        width: 100%;
        min-width: 750px;
        border-collapse: collapse;
        font-size: $font-size-sm;

        th, td {
          padding: 14px 18px;
          text-align: left;
          vertical-align: middle;
          border-bottom: 1px solid $border-color-light;
        }

        th {
          background-color: $table-header-bg;
          color: $text-secondary;
          font-weight: 500;
          font-size: $font-size-sm;
          position: sticky;
          top: 0;
          z-index: 10; // Ensure above scrolling content
          border-bottom-width: 1px; // Thinner but distinct header bottom border
          border-bottom-color: $border-color; // Slightly darker than cell borders
        }

        tbody tr {
          transition: background-color $transition-speed $transition-easing;
          &:hover { background-color: $table-row-hover-bg; }
          &:last-child td { border-bottom: none; }
        }

        td {
          color: $text-primary;
          &.user-name { font-weight: 500; }
          &.user-email { color: $primary-color; }

          .verification-status {
            display: inline-flex;
            align-items: center;
            font-size: 0.8rem;
            padding: 4px 10px; // Slightly more padding for pill
            border-radius: 16px; // More pill-like
            font-weight: 500;

            &.verified {
              background-color: lighten($success-color, 40%);
              color: darken($success-color, 5%);
              // SVG check icon would go here
            }
            &.not-verified {
              background-color: lighten($error-text, 40%); // Using error-text as base
              color: darken($error-text, 5%);
              // SVG cross icon would go here
            }
          }
        }
      }
    }
  }

  // Pagination Styling
  .pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: $spacing-sm; // Reduced top padding as table has bottom margin
    gap: 10px;

    button, .page-number, .ellipsis {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      min-width: 40px;
      height: 40px;
      padding: 0 10px;
      border: 1px solid $border-color;
      background-color: $card-bg;
      color: $text-secondary;
      border-radius: 8px; // More modern rounded corners
      font-size: $font-size-sm;
      font-weight: 500;
      transition: all $transition-speed $transition-easing;
      cursor: pointer;
      box-shadow: 0 1px 2px rgba(0,0,0,0.05); // Subtle shadow on buttons

      &:hover:not(:disabled):not(.current-page) {
        border-color: $primary-color;
        color: $primary-color;
        box-shadow: 0 2px 5px rgba($primary-color, 0.15);
        transform: translateY(-1px); // Slight lift on hover
      }
    }

    button:disabled {
      color: lighten($text-light, 15%);
      cursor: not-allowed;
      background-color: $table-header-bg;
      box-shadow: none;
      transform: none;
    }

    .page-number.current-page {
      background-color: $primary-color;
      color: #fff;
      border-color: $primary-color;
      font-weight: 600;
      box-shadow: 0 2px 6px rgba($primary-color, 0.25);
      transform: translateY(-1px);
    }

    .ellipsis {
      border: none;
      background-color: transparent;
      box-shadow: none;
      cursor: default;
      min-width: auto;
      padding: 0 8px;
      transform: none;
    }
  }
}

// --- Responsive Adjustments for User Management Page ---
@media (max-width: $bp-md) { // Medium devices (tablets)
  .user-management-page {
    .page-header .page-title { font-size: 1.6rem; }
    .table-container .table-responsive-wrapper .user-table {
      min-width: 650px;
      th, td { padding: 12px 15px; }
    }
    .pagination {
      gap: 8px;
      button, .page-number, .ellipsis { min-width: 38px; height: 38px; }
    }
  }
}

@media (max-width: $bp-sm) { // Small devices (phones)
  .user-management-page {
    padding: $spacing-sm;

    .page-header {
      flex-direction: column;
      align-items: flex-start;
      gap: $spacing-md; // More space when stacked
      padding-bottom: $spacing-md;
      margin-bottom: $spacing-md;

      .page-title { font-size: 1.4rem; }
      .header-actions button { width: 100%; text-align: center; } // Full width button on mobile
    }

    .table-container {
      border-radius: 8px; // Keep some radius on mobile too
      .table-responsive-wrapper .user-table {
        font-size: $font-size-sm - 0.1rem;
        min-width: 100%; // Allow shrinking
        th, td {
          padding: 10px 12px;
          white-space: normal; // Allow wrapping
        }
        // Optionally hide less critical columns on mobile:
        // .hide-on-mobile { display: none; }
        // Then add class="hide-on-mobile" to <th> and <td> of those columns
      }
    }
    .pagination {
      gap: $spacing-xs;
      flex-wrap: wrap;
      justify-content: center; // Center wrapped items
      button, .page-number, .ellipsis {
        min-width: 36px; height: 36px;
        font-size: $font-size-sm - 0.1rem;
        margin-bottom: $spacing-xs;
        flex-grow: 1; // Make prev/next buttons take more space if needed
        flex-basis: 40px; // Base size before growing
      }
      button:first-child, button:last-child { // Prev/Next buttons
        padding: 0 $spacing-md; // More horizontal padding
      }
    }
  }
}